import { formatDate } from '@angular/common';
import { TitleCasePipe } from '@angular/common';
import { HttpClient } from '@angular/common/http';
import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Observable } from 'rxjs';
import { startWith, map } from 'rxjs/operators';
import { BankServiceService } from 'src/app/core/services/bank-service.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { ConfirmedValidators } from 'src/app/_helpers/confirmed.validators';
import { Constants } from 'src/app/_helpers/constant';
import { CashInHandServiceService } from 'src/app/core/services/cash-in-hand-service.service';
import { SaleInvoiceService } from 'src/app/core/services/sale-invoice.service';
import * as moment from 'moment';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { AddBankComponent } from '../add-bank/add-bank.component';
import { AddCashComponent } from '../add-cash/add-cash.component';

@Component({
  selector: 'app-ban-kdetails',
  templateUrl: './ban-kdetails.component.html',
  styleUrls: ['./ban-kdetails.component.scss']
})
export class BanKDetailsComponent implements OnInit {
  public bankForm: FormGroup;
  public CashInHandForm: FormGroup;
  public filteredOptions: Observable<string[]>;
  public myControl = new FormControl();
  color: string = 'black';
  submitted: boolean;
  bankApiResponse: any;
  isEdit: boolean = false;
  isDisabled = false;
  isShowButton: Boolean = false;
  isShown: Boolean = false;
  isShownm: Boolean = false;
  bankdetails: any;
  bname: any = "";
  data: any
  mobileOtp: any;
  formData: any = [];
  branchCode: any;
  branchCode2: any;
  bankDetailByAcc: any;
  public hide: boolean = true;
  accoutNumber: any;
  public IFSC: any;
  public currentUser = JSON.parse(localStorage.getItem("currentUser"))

  public todaysDate;
  public today;
  partyId: any
  saleInvoiceId: any;
  public paymentType: string = "";
  public remark2: any;
  dialogRef: any;
  public singleBankRecord: any;
  singleBankName: any;
  singleBankAcc: any;
  singleIfscCode: any;


  constructor(private formBuilder: FormBuilder,
    public router: Router,
    private toastService: ToastNotificationService,
    private bankService: BankServiceService,
    private http: HttpClient,
    private modal: NgbModal,
    private titleCasePipe: TitleCasePipe,
    private cashInHandService: CashInHandServiceService,
    private saleInvoiceService: SaleInvoiceService, private dialog: MatDialog) { }

  ngOnInit(): void {
 
    this.getbankdetails();
    this.bankService.partyAdded$.subscribe(() => {
      this.getbankdetails();
    });
  }




  /**
   * Filter customer name search
   * @param name
   * @returns
   */
  private filterBankNames(name: string): string[] {
    return this.bankdetails.filter(x =>
      x.bankName.toLowerCase().indexOf(name.toLowerCase()) === 0);
  }

  getbankdetails() {
    this.bankService.getBankDetails({}, this.currentUser.id).then((res) => {
      this.bankApiResponse = res.data;
      this.bankApiResponse.forEach((ele) => {
        ele.isEnable = false;
      })
    },
      (err) => {
        if (err.error.expose) {
       //   this.bankApiResponse = [];
          this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else {
          this.toastService.openErrorSnackBar("Something Went Wrong.");
        }

      }
    )
  }

  getbankdetailsById(id) {
    // <p class="mb-1 mt-2"><span class="me-3">Bank Name :</span>ICICI Bank</p>
    // <p class="mb-2 mt-1">Account Number: 30002000668888</p>
    // <p class="mb-1 mt-1">IFSC Code: ICI2004300</p>
    // <p class="mb-1 mt-1 d-flex flex-row" style="justify-content: end">
    //   Balance: 200000.00
    // </p>
    this.bankService.getBankDetailsById({}, this.currentUser.id, id).then((res) => {

      this.singleBankRecord = res.data;
      this.singleBankName = this.singleBankRecord.bankName
      this.singleBankAcc = this.singleBankRecord.accountNumber
      this.singleIfscCode = this.singleBankRecord.ifscCode

    })

  }

  removeBanK() {
    this.bankService.deleteBankAccNo({}, this.accoutNumber).then((res) => {
      this.toastService.openSnackBar("Bank Record Deleted Successfully!!!");
      this.getbankdetails();
    })
  }

  myClickHandler(bankDetails) {
    this.bankApiResponse.forEach(element => {
      if (element.id == bankDetails.id) {
        element.isEnable = true;
      }
      else {
        element.isEnable = false;
      }
    });
  }

 


  modalDismiss() {
    this.bankForm.reset();
    this.modal.dismissAll();
    this.isEdit = false;
  }



  openDialogBank() {
    const dialogRef: MatDialogRef<AddBankComponent> = this.dialog.open(AddBankComponent, {
      width: 'auto',
      height: '700px',
      data: {},
      panelClass: 'custom-dialog-expense-class',
      disableClose: true,
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });


  }

  openDialogCash() {
    const dialogRef: MatDialogRef<AddCashComponent> = this.dialog.open(AddCashComponent, {
      width: 'auto',
      height: '700px',
      data: {},
      panelClass: 'custom-dialog-expense-class',
      disableClose: true,
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }

  
  cancel() {
    this.dialogRef.close();
  }
}
